.Controls {
  background: #6f7bd0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 2.5rem;
  bottom: 0;
  /* padding: 0 0.5rem; */
}

.PlayPause,
.FullScreen {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  width: 3rem;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* border-radius: 30px; */
  user-select: none;
  transition: background 0.2s ease-out;
  /* transform: scale(1.2);
  transition: transform 200ms ease 0s */
}

.PlayPause:focus,
.FullScreen:focus {
  outline: none;
}
.PlayPause:hover,
.FullScreen:hover {
  background: rgba(197, 202, 236, 0.2);
  background: rgba(0, 0, 0, 0.6);
}

.PlayPause {
  padding-left: 2px;
}